.promo {
  display: flex;
  justify-content: space-between;
  align-items: center;

  overflow: hidden;

  padding-bottom: 70px;
  padding-top: 100px;
}

.promo__img {
  padding-left: 20px;
  padding-right: 20px;

  background-image: url("../img/triangle.svg");
  background-size: 700px 700px;
  background-position: -387px -158px;
  padding-top: 30px;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .promo__img {
    width: 430px;
    height: 305px;
    margin-left: 66px;
    padding-bottom: 25px;

    background-position: -393px -160px;
  }
}

@media (min-width: 320px) and (max-width: 767px) {
  .promo {
    position: relative;
  }

  .promo__img {
    position: absolute;

    width: 301px;
    height: 220px;
    top: -26px;
    right: -41px;

    background-size: 450px 450px;
    background-position: -84px -84px;
  }
}
