.rights {
  padding-top: 21px;
  padding-bottom: 14px;
  opacity: 0.5;
  font-family: "PT Mono", sans-serif; }

.rights__link {
  color: #1b2d37;
  text-decoration: none; }

.rights__credits {
  display: flex; }

@media (min-width: 320px) and (max-width: 767px) {
  .rights {
    text-align: center; } }

.credits__element--copyright {
  margin-right: 59px; }

@media (min-width: 320px) and (max-width: 1023px) {
  .credits {
    display: none; } }

.modal-callback {
  position: fixed;
  content: "";
  top: 10%;
  left: 15%;
  display: block;
  width: 490px;
  padding: 30px 40px; }

.modal-callback__cross-button {
  display: block;
  margin-left: auto;
  background-color: transparent;
  border: none; }

.modal-callback__title {
  margin-top: 5px;
  font-size: 28px; }

.modal-callback__text {
  padding-right: 60px;
  font-size: 16px;
  font-family: "PT Mono", sans-serif; }

@media (min-width: 320px) and (max-width: 1023px) {
  .modal-callback {
    display: none; } }

.modal-form {
  width: 411px;
  margin: 0; }

.modal-form__input {
  width: 361px;
  margin-bottom: 20px;
  padding: 15px 25px;
  background-color: rgba(255, 255, 255, 0.1);
  border: none; }

.modal-form__input::placeholder {
  color: #ffffff; }

.modal-form__agreement {
  position: relative;
  margin-left: 25px; }

.button {
  padding: 10px 30px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #00b2ff;
  border: 1px solid #ffffff;
  border-radius: 5px; }

.button--modal {
  width: 100%; }

.button--feedback {
  min-width: 180px;
  margin-right: 60px; }

.checkbox + label::after {
  content: "";
  position: absolute;
  top: 5px;
  left: -25px;
  width: 16px;
  height: 16px;
  background-image: url("../img/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #ffffff; }

.checkbox--modal + label::after {
  top: 2px; }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", sans-serif;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 21px;
  box-sizing: border-box; }
